body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 100vw;
  max-width: 100%;
  background-color: rgb(250, 250, 250);
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wrapper {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
    transition: 0.5s;
}
.app-wrapper-transition {
    background-color: rgba(0, 0, 0, 0.8);
    transition: 0.5s;
}
.header {
    position: fixed;
    width: 100%;
    height: 25px;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: rgba(255, 255, 255, 0);
    z-index: 9999;
}
.header:hover {
    color: rgba(255, 255, 255, 1);
}

.header-item-active {
    width: 49%;
}
.header-item-inactive {
    width: 17%;
}
.header-item-transition {
    width: 25%;
}
.header-item-active, .header-item-inactive, .header-item-transition {
    transition: 0.5s;
    height: 100%;
    cursor: pointer;
    text-align: center;
    line-height: 25px;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 25px;
    overflow: hidden;
}
@media only screen and (max-width: 800px) {
    .header-item-active, .header-item-inactive, .header-item-transition {
        font-size: 12px;
    }
}
.fade-in {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
    transition: 0.5s;
}
.fade-in-backswing {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
    transition: 0.5s;
}
.stretch {
    width: 0 !important;
    opacity: 0;
    transition: 0.5s;
}
.stretch-backswing {
    opacity: 1;
    transition: 0.5s;
}
.instruction-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    width: 100%;
    height: calc(50% - 50px * 0.5);
    overflow: hidden;
}
.instruction-wrapper img {
    width: 100%;
    max-width: 100px;
    margin: 10px 0 20px 0;
    height: 100%;
    max-height: 100px;
}
.instruction-highlight {
    font-weight: 700;
    cursor: help;
    position: relative;
}
.instruction-highlight-info-active {
    opacity: 1;
    transition: 0.5s;
    transition-delay: 0.5s;
}
.instruction-highlight-info-inactive {
    opacity: 0;
    transition: 0.5s;
}
.instruction-highlight-info-active, .instruction-highlight-info-inactive {
    position: absolute;
    bottom: calc(100% + 50px);
    right: 0;
    width: 500px;
    font-weight: 500;
    color: black;
    cursor: auto;
    background-color: white;
    padding: 25px;
    border-radius: 25px;
}
.instruction-highlight-info-active-pointer-1, .instruction-highlight-info-active-pointer-2, .instruction-highlight-info-active-pointer-3 {
    opacity: 1;
    content: '';
    background-color: white;
    position: absolute;
}
.instruction-highlight-info-inactive-pointer-1, .instruction-highlight-info-inactive-pointer-2, .instruction-highlight-info-inactive-pointer-3 {
    opacity: 0;
    transition: 0s;
    width: 0;
    height: 0;
    position: absolute;
}
.instruction-highlight-info-active-pointer-1 {
    bottom: calc(100% + 5px);
    right: 60px;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    transition: 0.1s;
}
.instruction-highlight-info-active-pointer-2 {
    bottom: calc(100% + 15px);
    right: 40px;
    width: 14px;
    height: 14px;
    border-radius: 7px;
    transition: 0.1s;
    transition-delay: 0.1s;
}
.instruction-highlight-info-active-pointer-3 {
    bottom: calc(100% + 25px);
    right: 60px;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    transition: 0.1s;
    transition-delay: 0.3s;
}
@media only screen and (max-width: 800px) {
    .instruction-wrapper {
        font-size: 8px;
    }
    .instruction-highlight-info-active, .instruction-highlight-info-inactive {
        width: 200px;
    }
    .instruction-wrapper img {
        max-height: 20px;
        max-width: 20px;
        margin: 5px 0 10px 0;
    }
    .instruction-highlight-info-active-pointer-1 {
        bottom: calc(100% + 2px);
        right: 30px;
        width: 2px;
        height: 2px;
        border-radius: 1px;
    }
    .instruction-highlight-info-active-pointer-2 {
        bottom: calc(100% + 4px);
        right: 25px;
        width: 4px;
        height: 4px;
        border-radius: 2px;
    }
    .instruction-highlight-info-active-pointer-3 {
        bottom: calc(100% + 8px);
        right: 30px;
        width: 6px;
        height: 6px;
        border-radius: 3px;
    }
    .instruction-highlight-info-active, .instruction-highlight-info-inactive {
        width: 300px;
        font-weight: 500;
        padding: 2px;
        border-radius: 5px;
        position: fixed;
        bottom: 65px;
        left: calc(50% - 150px);
    }
}
.app-form {
    width: 100%;
    height: 50px;
    max-width: 800px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
}
.app-form-input {
    width: 60%;
    margin: 0 2.5% 0 5%;
    height: 100%;
    font-size: 24px;
    border-radius: 25px;
    border: 4px solid black;
    padding: 0 25px;
    background-color: rgba(0, 0, 0, 0);
    font-weight: 700;
}
.app-form-input:focus {
    outline-width: 0;
}
.app-form-submit {
    width: 30%;
    height: 100%;
    border: 4px solid black;
    margin: 0 5% 0 2.5%;
    font-size: 24px;
    border-radius: 25px;
    font-weight: 700;
    cursor: pointer;
    box-sizing: content-box;
    -moz-box-sizing: content-box;
    background-color: rgba(0, 0, 0, 0);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
    overflow: hidden;
}
.app-form-submit:focus {
    outline-width: 0;
}
.app-form-submit:hover {
    border: 4px solid red;
    background-color: red;
    color: white;
}
.app-form-submit-disabled {
    opacity: 0.5;
    cursor: progress;
}
.app-form-submit-disabled:hover {
    border: 4px solid black !important;
    background-color: white !important;
    color: black !important;
}
.app-form-message {
    height: calc(50% - 25px - 100px);
    padding-top: 100px;
    width: 100%;
    text-align: center;
    font-weight: 700;
}
.app-form-submit-error-timer-inactive, .app-form-submit-error-timer-active {
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(255, 0, 0, 0.5);
    z-index: -1;
}
.app-form-submit-error-timer-inactive {
    width: 0;
}
.app-form-submit-error-timer-active {
    width: 100%;
    -webkit-animation-name: error-timer;
            animation-name: error-timer;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}
@-webkit-keyframes error-timer {
    0%   {width: 100%;}
    100% {width: 0;}
}
@keyframes error-timer {
    0%   {width: 100%;}
    100% {width: 0;}
}
@media only screen and (max-width: 800px) {
    .app-form {
        height: 25px;
    }
    .app-form-input {
        font-size: 16px;
        padding: 0 15px;
        border: 2px solid black;
        font-weight: 500;
    }
    .app-form-submit {
        font-size: 16px;
        border: 2px solid black;
        font-weight: 500;
    }
    .app-form-submit:hover {
        border: 2px solid red;
    }
    .app-form-message {
        height: calc(50% - 25px - 30px);
        padding-top: 30px;
        font-weight: 500;
        font-size: 16px;
    }
    .app-form-submit-disabled:hover {
        border: 2px solid black !important;
    }
}
